body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    padding: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    font-size: 14px;
}

.App-loading {
    color: black;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}

.Movie {
    border-style: solid;
    border-color: grey;
    padding: 0 20px;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-bottom: 50px;
    width: 40%;
    text-overflow: ellipsis;
    box-shadow: 0 8px 38px rgba(133, 133 ,133, 0.3), 0 5px 12px rgba(133, 133, 133, 0.22);
}

.Movie_Columns {
    width: 30%;
    box-sizing: border-box;
    text-overflow: ellipsis;
}

.Movie_Columns:last-child {
    padding: 20px 0;
    width: 60%;
}

.Movie h1{
    font-size: 20px;
    font-weight: 600;
}

.Moive .Movie_Genres {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-bottom: 20px;
}

.Movie_Genres .Movie_Genre {
    margin-right: 10px;
    color: #B4B5B0;
}

.Movie .Movie_Synopsis {
    text-overflow: ellipsis;
    color: #B4B5B0;
    overflow: hidden;
}

.Movie .Movie_Poster {
    max-width: 100%;
    position: relative;
    top: -20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80, 80, 80, 0.22);
}

@media screen and (min-width: 320px) and (max-width: 667px) {
    .Movie {
        width: 100%;
    }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
    .Movie {
        width: 100%;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .Movie_poster {
        top: 0;
        left: 0;
        width: 100%;
    }
    .Movie_Columns {
        width: 100%!important;
    }
}
